.about {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  margin: auto;
  text-align: center;
  margin-top: -20px;
}
.about-button {
  background-color: #facf0f;
  width: 183px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin: auto;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.about-button:hover {
  background-color: #bca754;
}

.about--mainContent {
  display: flex;
  justify-content: center;
  column-gap: 15%;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 40%;

  margin-left: 10px;
  margin-right: auto;

  position: relative;

  scroll-behavior: smooth;

  /*border: 1px solid black;*/
}

.content--two {
  width: 70%;
  height: 60%;
  /* border: 1px solid black;*/
  text-align: left;
  background-color: #fafafa;
  border-radius: 10px;
  padding-left: 20px;
}
.content--one {
  text-align: left;
  width: 70%;
  height: 50%;
  background-color: #fafafa;
  border-radius: 10px;
  padding-left: 20px;
  padding-right: 20px;
}

.skillsContainer {
  display: flex;
  flex-wrap: wrap;
}

.skills {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  column-gap: 10px;
  row-gap: 10px;
}
.skills > button {
  margin: 5px;
  padding: 5px 20px 5px 20px;
  border-radius: 20px;
  border-color: transparent;
  box-shadow: 5px 5px 5px -2px;
}

.skills > div {
  padding: 20px 20px 5px 20px;
  border-radius: 5px;
  border: 0.5px solid black;
  justify-content: center;
  height: 50px;
  width: 40px;
  display: flex;
  flex-direction: column;
  column-gap: 10px;

  align-items: center;
  box-shadow: 2px 2px 2px -1px;

  transition: width 0.5s, height 0.5s;
}
.skills > div:hover {
  width: 52px;
  height: 56px;
  filter: brightness(0.7) contrast(0.7);
}
.skills > p {
  text-align: center;
  justify-content: center;
}
.tech {
  margin-top: 20px;
}
.tech > button {
  margin: 5px;
  color: white;
  padding: 5px 10px 5px 10px;
  border-radius: 20px;
  border-color: transparent;
  font-size: 10px;
  background-color: rgb(84, 84, 87);
}

.skills > button:hover {
  background-color: #facf0f;
  border-radius: 20px;
  border-color: transparent;
}

.about--heading {
  margin-top: 20px;
}
.about--heading > p {
  font-size: 18px;
}

.about > p {
  font-size: 16%;
}
.aboutMe {
  width: 100%;
}
.underline {
  width: 30px;
  background-color: #facf0f;
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #facf0f;
}
@media only screen and (min-width: 1200px) {
  .about--heading > h3 {
    font-size: 30px;
  }

  .content--one > h4 {
    font-size: 20px;
  }
  .content--two > h4 {
    font-size: 20px;
  }
  .content--one > p {
    font-size: 14px;
  }
  .content--two > p {
    font-size: 18px;
  }
  .about > p {
    font-size: 18px;
  }
  .aboutMe {
    width: 100%;
  }

  .about--mainContent {
    column-gap: 5%;
    height: 50%;
  }
  .skills > div {
    font-size: 18px;
    padding: 30px 30px 10px 30px;
  }
}
@media only screen and (min-width: 768px) {
  .about--mainContent {
    column-gap: 10px;
    height: 50%;
    overflow-y: scroll;
    max-height: 200vh;
  }
}
