.imageStyle {
  height: 30%;
  width: 15%;
  background-color: #facf0f;
  border-radius: 50%;
  margin-left: 40px;
  display: flex;
  text-align: center;
  justify-content: center;
}
img {
  height: 70%;
  width: 70%;
  margin: auto;
}
.socialContainer {
  height: 40vh;
}
.social {
  display: flex;
  justify-content: space-around;

  align-items: center;

  align-items: center;
  width: 70%;
  height: 15vh;
  text-align: left;
  margin-left: auto;
  margin-right: auto;
}
.content {
  display: flex;
  flex-direction: column;
  margin: auto;
  text-align: left;
  align-items: center;
}
.form {
  display: flex;
  flex-direction: column;
  height: 80%;
  width: 50%;
  gap: 20px;
  margin-left: 10%;
  margin-right: auto;

  margin-top: 2%;
  padding-top: 2%;
  padding-left: 4%;
  padding-bottom: 1%;
  border-bottom: 30px;
  border-radius: 10px;
  background-color: #ffffff;
  border-style: none;
}
.contact {
  display: flex;
}
.form > input {
  height: 20%;
  width: 90%;
  border-radius: 5px;
  background-color: #fafafa;
  border-style: none;

  box-shadow: 5px 5px 5px lightgray;
}
.form > textarea {
  width: 90%;
  height: 30%;
  background-color: #fafafa;
  border-style: none;
  box-shadow: 5px 5px 5px lightgray;
}

.contact-button {
  background-color: #facf0f;
  width: 183px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin-top: 20px;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.contact-button:hover {
  background-color: black;
  color: #facf0f;
}
.content > a {
  font-size: 1rem;
}
.content > p {
  font-size: 1rem;
}
.modal {
  margin: auto;
  color: rgb(159, 23, 23);
  font-size: 7px;
  text-align: left;
}
@media only screen and (max-width: 768px) {
  .social {
    display: block;
    width: 50%;
    height: 40%;
    margin-left: 20%;
    margin-right: auto;
  }
  .social > div > p {
    font-size: x-small;
  }
  .social > div > a {
    font-size: x-small;
  }
  .contact-button {
    width: 100px;
  }
  .form {
    padding-left: 20px;
    width: 60%;
    height: 100%;
    margin-left: 20px;
  }
  .imageStyle {
    height: 30px;
    width: 30px;
  }
}
