.navBar {
  width: 100%;
  height: 62px;
  background-color: #ffff;
  margin-top: 0;
  display: flex;
  justify-content: space-between;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
}
.name {
  margin-left: 40px;
}
.navItems {
  color: black;
  font-size: 16px;
  margin: auto 40px auto auto;
  text-align: center;
  display: flex;
  gap: 20px;
  font-weight: bold;
}

.home {
  display: flex;
  height: 100%;
  width: 100%;
  overflow-y: hidden;
}
.profile {
  margin-top: 60px;
  height: 700px;
  width: 300px;
}
.profilepic {
  height: 50%;
  width: 100%;
}
.home-button {
  background-color: #facf0f;
  width: 183px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin-top: 20px;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.home-button:hover {
  background-color: black;
  color: #facf0f;
}
.mainContent {
  display: flex;

  margin: 20vh auto auto auto;
  text-align: center;
  width: 484px;
  height: 250px;
}
.mainContent > div > p {
  font-size: 15px;
}
.mainContent > div > h1 {
  font-size: 40px;
  overflow: hidden;
  white-space: nowrap;
  width: 100%;

  animation: typing;
  animation-duration: 1.5s;
  animation-delay: 0.5s;
  animation-timing-function: steps(30, end);
  animation-fill-mode: forwards;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

.socialMedia {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 180px auto auto 0;
  background-color: #facf0f;
  padding: 10px;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
@media only screen and (max-width: 768px) {
  .mainContent > div > h1 {
    font-size: 20px;
    width: 100%;
  }

  .mainContent {
    width: 100%;
  }
  .home {
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 100vh;
  }
  .profile {
    width: 100%;
  }
}
@media only screen and (min-width: 768px) {
  .mainContent > div > h1 {
    width: 100%;
  }
  .profile {
    margin: 15vh auto auto auto;
  }
  .profilepic {
    height: 100%;
  }
  .socialMedia {
    margin-top: 30vh;
  }

  .mainContent > div > p {
    font-size: 18px;
    width: 100%;
  }
  .home {
    column-gap: 10px;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 100vh;
  }
}

/* Medium devices (landscape tablets, 768px and up) */

@media only screen and (min-width: 1200px) {
  .home {
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 100vh;
  }
  .profile {
    height: 1300px;
    width: 400px;
  }
  .profilepic {
    height: 80%;
    width: 100%;
  }
  .mainContent {
    margin-right: 30%;
  }
  .mainContent > div > h1 {
    font-size: 60px;
  }
  .mainContent > div > p {
    font-size: 20px;
  }
  .name {
    margin-left: 40px;
    font-size: 20px;
  }
  .socialMedia {
    height: 80px;
    width: 40px;
  }
}
